window.$ = window.jQuery = function (selectorORArray) {
    let elements
    if (typeof selectorORArray === 'string') {
        elements = document.querySelectorAll(selectorORArray)
    } else if (selectorORArray instanceof Array) {
        elements = selectorORArray
    }
    // ÂàõÂª∫apiÂØπË±°,‰ªñÁöÑÂéüÂûã‰∏∫jQuery.prototype
    let api = Object.create(jQuery.prototype)
    this.Object.assign(api, {
        elements: elements,
        oldApi: selectorORArray.oldApi
    })
    // ËøîÂõûapi,Êñπ‰æøÈìæÂºèÊìç‰Ωú
    return api
}

//‰∏ãÈù¢ÁöÑÂÜôÊ≥ï,Áî±‰∫éÊØè‰∏™jQueryÂØπË±°ÈÉΩ‰ºöÂàõÂª∫Áõ∏ÂêåÂäüËÉΩÁöÑÂáΩÊï∞,ÂæàÊµ™Ë¥πÂÜÖÂ≠ò,ÊâÄ‰ª•ÂèØ‰ª•Â∞ÜÂáΩÊï∞ÂÜôÂà∞ÂéüÂûã‰∏ä
jQuery.prototype = {
    constructor: jQuery,
    jQuery: true,
    // 1.ÈÅçÂéÜDOMÂØπË±°
    each(fn) {
        for (let i = 0; i < this.elements.length; i++) {
            fn.call(null, this.elements[i], i)
        }
    },
    //2.‰∏∫DOMÂØπË±°Â¢ûÂä†Á±ª
    addClass(className) {
        // Èó≠ÂåÖ,Âõ†‰∏∫Êú¨ÂáΩÊï∞Áî®Âà∞‰∫ÜÂ§ñÈù¢ÂáΩÊï∞ÁöÑÂèòÈáèelements,elements‰∏ç‰ºöË¢´ÂõûÊî∂
        // for (let i = 0; i < elements.length; i++) {
        //     elements[i].classList.add(className)
        // }
        this.each((node) => {
            node.classList.add(className)
        })
        return this // api
    },
    //3.ÊâæÂà∞Âê´ÊúâËØ•ÈÄâÊã©Âô®ÁöÑÊâÄÊúâDOMÂØπË±°
    // ÊØîÂ¶Ç: jQuery('.test).find('.child')
    // Ê≠§Êó∂Êìç‰ΩúÁöÑÊòØ.text .childÁöÑDOMÂÖÉÁ¥†ÁöÑÈõÜÂêà,ÊâÄ‰ª•Ë¶ÅÊääelementsÂÄº‰øÆÊîπ‰∏∫ÈõÜÂêàÁöÑÂÄº
    find(selector) {
        let array = []
        array.oldApi = this
        this.each((node) => {
            array.push(...node.querySelectorAll(selector))
        })
        return jQuery(array)
    },
    //4.ÂõûÂà∞‰∏ä‰∏Ä‰∏™jQueryÂØπË±°
    end() {
        if (this.oldApi) {
            return this.oldApi
        } else {
            return this
        }
    },
    parent() {
        let array = []
        this.each((node) => {
            if (array.indexOf(node.parentNode) === -1) {
                array.push(node.parentNode)
            }
        })
        return jQuery(array)
    },
    children() {
        let array = []
        this.each((node) => {
            array.push(...node.children)
        })
        return jQuery(array)
    },
}




